import styles from "./Input.module.css";

const Input = (props) => {
  const onInputChangeHandler = (event) => {
    !props.type && props.modifyAmount(event.target.value < 0 ? 1 : event.target.value);
    props.onChangeHandler(event.target.value);
  };

  const onInputBlurHandler = (event) => {
    props.onBlurHandler && props.onBlurHandler(true);
  };

  return (
    <input
      type={props.type}
      onChange={onInputChangeHandler}
      onBlur={onInputBlurHandler}
      value={props.amount}
      className={`${styles.input} ${styles[props.size]} ${styles[props.textAlign]} ${
        styles[props.validity]
      }`}
    ></input>
  );
};

export default Input;
