import Button from "../UI/Button";
import Modal from "../UI/Modal";
import CartItem from "./CartItem";
import { useContext, useState } from "react";
import CartContext from "../../store/cart-context";
import styles from "./CartModal.module.css";
import InputGroup from "../UI/InputGroup";

function textValidation(value) {
  return !(value.trim() === "");
}

function postalValidation(value) {
  return isFinite(value) && value.toString().length === 6;
}

const CartModal = (props) => {
  const cartContext = useContext(CartContext);
  const [nameInvalid, setNameInvalid] = useState(false);
  const [streetInvalid, setStreetInvalid] = useState(false);
  const [postalInvalid, setPostalInvalid] = useState(false);
  const [cityInvalid, setCityInvalid] = useState(false);
  const [formSubmit, setFormSubmit] = useState(false);
  const [displayForm, setDisplayForm] = useState(false);

  let hasErrorForm;

  const formSubmitHandler = () => {
    setFormSubmit(true);

    hasErrorForm = nameInvalid || streetInvalid || postalInvalid || cityInvalid;

    if (!hasErrorForm) {
      console.log("Form submitted successfully");
    }
  };

  return (
    <Modal>
      {cartContext.foodData.cartItems.map((ele) => (
        <CartItem key={Math.random()} itemInfo={ele} />
      ))}
      <div className={styles["cart__price"]}>
        <h3>Total Amount</h3>
        <h3>
          $
          {cartContext.foodData.cartItems
            .reduce((acc, ele) => {
              const totalPrice = parseFloat(ele.price.slice(1)) * ele.amount;
              return (acc += totalPrice);
            }, 0)
            .toFixed(2)}
        </h3>
      </div>
      {displayForm && (
        <form className={styles["cart__form"]}>
          <InputGroup
            validityConnection={setNameInvalid}
            validation={textValidation}
            name="name"
            label="Your Name"
            formSubmit={formSubmit}
          />
          <InputGroup
            validityConnection={setStreetInvalid}
            validation={textValidation}
            name="street"
            label="Street"
            formSubmit={formSubmit}
          />
          <InputGroup
            validityConnection={setPostalInvalid}
            validation={postalValidation}
            name="postal code"
            label="Postal Code"
            formSubmit={formSubmit}
          />
          <InputGroup
            validityConnection={setCityInvalid}
            validation={textValidation}
            name="city"
            label="City"
            formSubmit={formSubmit}
          />
          <div className={styles["form__controls"]}>
            <Button
              btnHandler={() => setDisplayForm(false)}
              size="large"
              variation="outline-rounded"
            >
              Cancel
            </Button>
            <Button
              disabled={hasErrorForm}
              formType="formSubmit"
              btnHandler={formSubmitHandler}
              size="large"
              color="light"
            >
              Confirm
            </Button>
          </div>
        </form>
      )}
      {!displayForm && (
        <div className={styles["cart__controls"]}>
          <Button btnHandler={props.toggleCartDisplay} size="large" variation="outline-rounded">
            Close
          </Button>
          <Button btnHandler={() => setDisplayForm(true)} size="large" color="light">
            Order
          </Button>
        </div>
      )}
    </Modal>
  );
};

export default CartModal;
