import { useEffect } from "react";
import useInput from "../Hooks/useInput";
import Input from "./Input";
import styles from "./InputGroup.module.css";

const InputGroup = (props) => {
  const { inputValue, setInputValue, setInputTouched, inputInvalid } = useInput(props.validation);
  props.validityConnection(inputInvalid);
  useEffect(() => {
    if (props.formSubmit) {
      setInputTouched(true);
    }
  }, [props.formSubmit]);

  return (
    <div>
      <label>
        <h6>{props.label}</h6>
      </label>
      <Input
        value={inputValue}
        onChangeHandler={setInputValue}
        onBlurHandler={setInputTouched}
        textAlign="left"
        type="form"
        size="large"
        validity={inputInvalid ? "invalid" : ""}
      ></Input>
      {inputInvalid && <p className={styles.invalid}>Please enter a valid {props.name}!</p>}
    </div>
  );
};

export default InputGroup;
