import styles from "./Button.module.css";

const Button = (props) => {
  function onClickHandler(event) {
    event.preventDefault();
    props.btnHandler &&
      props.formType !== "formSubmit" &&
      props.btnHandler({
        ...props.itemInfo,
        amount: props.orderAmount ? +props.orderAmount : 1,
      });
    props.formType === "formSubmit" && props.btnHandler();
  }
  return (
    <button
      onClick={onClickHandler}
      className={`${styles.button} ${styles[props.size]} ${styles[props.variation]} ${
        styles[props.color]
      }`}
      {...props}
    >
      {props.children}
    </button>
  );
};

export default Button;
